<template>
    <div class="row mt-3 mb-3">
        <div class="col-4 offset-1">
            <div>{{ user.username }} (UID: {{ user.uid }})</div>
            <div>
                
                <span v-bind:class="{'text-success': user.online,'text-danger':user.banned}">
                    {{ status }}
                </span>
            </div>
        </div>
        <button class="btn col-2 offset-1" v-bind:class="{'btn-outline-danger':!user.banned,
        'btn-outline-primary':user.banned}">{{ banText }}</button>
        <button class="btn btn-outline-info col-2 offset-1"></button>
    </div>
</template>

<script>
    export default {
        name: 'UserItem',
        props: ['user'],
        computed: {
            status: function () {
                if(this.user.banned){
                    return '';
                }
                if(this.user.online){
                    return '';
                }else{
                    return '';
                }
            },
            banText: function () {
                if(this.user.banned){
                    return '';
                }
                return '';
            }
        }
    }
</script>

